.sidebar {
   padding: 10px;
   grid-area: sidebar;
   background-color: rgb(0 0 0);
}

.sidebarItem {
   margin-top: 20px;
   margin-bottom: 20px;

   width: 140px;
   height: 40px;

   display: flex;

   align-items: center;

   background-color: #fff;

   padding-left: 10px;

   border-radius: 10px;

   transition: all 0.3s ease-in;
}

.sidebarItem:hover {
   transform: scale(1.1);
}

.sidebarItem:last-child {
   margin-bottom: 0;
}

.sidebarLink {
   font-size: 20px;
   background-color: rgb(255 255 255);
   color: rgb(255 255 255);
   text-transform: uppercase;
   text-shadow: 2px 2px 5px rgb(0 0 0);
}
.sidebarLink{
   transition: all .3s ease;
}
.activeLink{
   color:goldenrod;
   text-shadow: none;
}